
.ui-menu-option{display:block;font-family:Roboto,-apple-system,BlinkMacSystemFont,"Segoe UI",Oxygen,Ubuntu,Cantarell,"Fira Sans","Droid Sans",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";position:relative;user-select:none;width:100%
}
.ui-menu-option.is-divider{background-color:rgba(0,0,0,0.08);display:block;height:.0625rem;margin:.375rem 0;padding:0
}
.ui-menu-option:not(.is-divider){color:rgba(0,0,0,0.87);cursor:pointer;font-size:.9375rem;font-weight:normal;min-height:2.5rem;outline:none
}
.ui-menu-option:not(.is-divider):hover:not(.is-disabled),body[modality="keyboard"] .ui-menu-option:not(.is-divider):focus{background-color:#EEEEEE
}
.ui-menu-option:not(.is-divider).is-disabled{color:rgba(0,0,0,0.54);cursor:default;opacity:0.5
}
.ui-menu-option:not(.is-divider).is-disabled .ui-menu-option__secondary-text{color:rgba(0,0,0,0.54)
}
.ui-menu-option__content{align-items:center;display:flex;height:2.5rem;padding:0 1rem
}
.ui-menu-option__icon{color:rgba(0,0,0,0.54);font-size:1.125rem;margin-right:1rem
}
.ui-menu-option__text{white-space:nowrap;overflow:hidden;text-overflow:ellipsis;flex-grow:1
}
.ui-menu-option__secondary-text{color:rgba(0,0,0,0.38);flex-shrink:0;font-size:.8125rem;margin-left:.25rem
}



