
.ui-calendar-week{display:flex;font-size:.875rem;width:100%
}
.ui-calendar-week__date{align-items:center;border-radius:50%;cursor:pointer;display:flex;height:2.25rem;justify-content:center;outline:none;text-align:center;width:2.25rem
}
.ui-calendar-week__date:hover,body[modality="keyboard"] .ui-calendar-week__date:focus{background-color:rgba(0,0,0,0.1)
}
.ui-calendar-week__date.is-in-other-month{visibility:hidden
}
.ui-calendar-week__date.is-today{font-weight:bold
}
.ui-calendar-week__date.is-today.is-selected{color:white
}
.ui-calendar-week__date.is-selected{color:white
}
.ui-calendar-week__date.is-disabled{background-color:transparent;cursor:default;opacity:0.4
}



