#page-loader{display:none
}
body.page-loading #page-loader{display:block
}
body.page-loading #app{opacity:0;display:none;transition:all 2s
}
body.page-loading .side-nav{display:none;opacity:0
}
.block-loader{display:none
}
.block-loading .loader-block{width:100px;height:100px;background-color:#b0bec5;margin:150px auto;animation:loader-block-animation 2s infinite ease-in-out
}
@keyframes loader-block-animation{
0%{transform:perspective(120px) rotateX(0deg) rotateY(0deg)
}
50%{transform:perspective(120px) rotateX(-180.1deg) rotateY(0deg)
}
100%{transform:perspective(120px) rotateX(-180deg) rotateY(-179.9deg)
}
}
.block-loading .block-loader{display:block
}
.block-loading .block-loader-content,.block-loading>.clearfix{display:none;transition:all 2s
}
.v-spinner .v-beat{-webkit-animation:v-beatStretchDelay 0.7s infinite linear;animation:v-beatStretchDelay 0.7s infinite linear;-webkit-animation-fill-mode:both;animation-fill-mode:both;display:inline-block
}
.v-spinner .v-beat-odd{animation-delay:0s
}
.v-spinner .v-beat-even{animation-delay:0.35s
}
@-webkit-keyframes v-beatStretchDelay{
50%{-webkit-transform:scale(0.75);transform:scale(0.75);-webkit-opacity:0.2;opacity:0.2
}
100%{-webkit-transform:scale(1);transform:scale(1);-webkit-opacity:1;opacity:1
}
}
@keyframes v-beatStretchDelay{
50%{-webkit-transform:scale(0.75);transform:scale(0.75);-webkit-opacity:0.2;opacity:0.2
}
100%{-webkit-transform:scale(1);transform:scale(1);-webkit-opacity:1;opacity:1
}
}
