
.ui-fab{align-items:center;border-radius:50%;border:none;box-shadow:0 2px 5px 0 rgba(0,0,0,0.2),0 2px 10px 0 rgba(0,0,0,0.16);cursor:pointer;display:flex;justify-content:center;outline:none;position:relative;transition:box-shadow 0.3s ease;z-index:30
}
.ui-fab::-moz-focus-inner{border:0
}
.ui-fab:hover,body[modality="keyboard"] .ui-fab:focus{box-shadow:0 8px 17px 0 rgba(0,0,0,0.25),0 6px 20px 0 rgba(0,0,0,0.2)
}
body[modality="keyboard"] .ui-fab:focus .ui-fab__focus-ring{opacity:1;transform:scale(1)
}
.ui-fab .ui-ripple-ink{border-radius:50%
}
.ui-fab__icon{height:initial;margin:0;position:relative;width:100%;z-index:1
}
.ui-fab__focus-ring{border-radius:50%;left:0;opacity:0;position:absolute;top:0;transform-origin:center;transform:scale(0);transition:transform 0.2s ease, opacity 0.2s ease
}
.ui-fab--size-normal,.ui-fab--size-normal .ui-fab__focus-ring{height:3.5rem;width:3.5rem
}
.ui-fab--size-small,.ui-fab--size-small .ui-fab__focus-ring{width:2.5rem;height:2.5rem
}
.ui-fab--color-default{background-color:white;color:rgba(0,0,0,0.54)
}
.ui-fab--color-default .ui-fab__icon{color:rgba(0,0,0,0.54)
}
.ui-fab--color-default .ui-ripple-ink__ink{opacity:0.2
}
.ui-fab--color-default .ui-fab__focus-ring{background-color:rgba(0,0,0,0.15)
}
.ui-fab--color-primary,.ui-fab--color-accent{color:white
}
.ui-fab--color-primary .ui-fab__icon,.ui-fab--color-accent .ui-fab__icon{color:white
}
.ui-fab--color-primary .ui-ripple-ink__ink,.ui-fab--color-accent .ui-ripple-ink__ink{opacity:0.4
}
.ui-fab--color-primary{background-color:#2196f3
}
.ui-fab--color-primary .ui-fab__focus-ring{background-color:#0c7cd5
}
.ui-fab--color-accent{background-color:#d500f9
}
.ui-fab--color-accent .ui-fab__focus-ring{background-color:#a900c6
}



