
.ui-preloader{position:relative;width:100%
}
.ui-preloader.is-loading .ui-preloader__progressbar{opacity:1;padding-top:.1875rem
}
.ui-preloader__progressbar{animation:ui-preloader-background linear 3s infinite;background-color:#159756;left:0;opacity:0;position:absolute;top:0;transition-duration:0.3s;transition-property:opacity, padding-top;transition-timing-function:ease;width:100%;overflow:hidden
}
.ui-preloader__progressbar::before,.ui-preloader__progressbar::after{animation:ui-preloader-front linear 3s infinite;content:'';display:block;height:.1875rem;position:absolute;top:0;width:100%;z-index:1
}
.ui-preloader__progressbar::before{right:50%;transform-origin:right
}
.ui-preloader__progressbar::after{left:50%;transform-origin:left
}
@keyframes ui-preloader-background{
0%,24.9%{background-color:#159756
}
25%,49.9%{background-color:#da4733
}
50%,74.9%{background-color:#3b78e7
}
75%,100%{background-color:#fdba2c
}
}
@keyframes ui-preloader-front{
0%{transform:scaleX(0);background-color:#da4733
}
24.9%{transform:scaleX(0.5);background-color:#da4733
}
25%{transform:scaleX(0);background-color:#3b78e7
}
49.9%{transform:scaleX(0.5);background-color:#3b78e7
}
50%{transform:scaleX(0);background-color:#fdba2c
}
74.9%{transform:scaleX(0.5);background-color:#fdba2c
}
75%{transform:scaleX(0);background-color:#159756
}
100%{transform:scaleX(0.5);background-color:#159756
}
}



