
.ui-calendar-month{height:16rem;overflow:hidden;position:relative;width:100%
}
.ui-calendar-month__header{align-items:center;display:flex;height:2.5rem;justify-content:space-around;width:100%
}
.ui-calendar-month__header span{align-items:center;color:rgba(0,0,0,0.54);display:flex;font-size:.875rem;height:2.25rem;justify-content:center;text-transform:uppercase;width:2.25rem
}
.ui-calendar-month__week{position:absolute;width:15.75rem
}
.ui-calendar-month__week.is-current{left:0
}
.ui-calendar-month__week.is-other{left:15.75rem
}
.ui-calendar-month__week.is-other.ui-calendar-month--slide-right{left:-15.75rem
}
.ui-calendar-month__week.is-sliding{transition:transform 250ms ease
}
.ui-calendar-month__week.is-sliding.ui-calendar-month--slide-left{transform:translate3d(-15.75rem, 0, 0)
}
.ui-calendar-month__week.is-sliding.ui-calendar-month--slide-right{transform:translate3d(15.75rem, 0, 0)
}



