
.ui-ripple-ink{border-radius:inherit;bottom:0;display:block;left:0;overflow:hidden;position:absolute;right:0;top:0;-webkit-mask-image:-webkit-radial-gradient(circle, white, black)
}
.ui-ripple-ink__ink{background-clip:padding-box;background-color:currentColor;border-radius:50%;height:0;opacity:0.2;pointer-events:none;position:absolute;transform:scale(0);transition:transform 0.6s ease-out, opacity 0.6s ease-out;user-select:none;width:0
}
.ui-ripple-ink__ink.is-held{opacity:0.4;transform:scale(1)
}
.ui-ripple-ink__ink.is-done{opacity:0 !important
}



