
.ui-autocomplete-suggestion{cursor:pointer;font-family:Roboto,-apple-system,BlinkMacSystemFont,"Segoe UI",Oxygen,Ubuntu,Cantarell,"Fira Sans","Droid Sans",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";font-size:.9375rem;padding:.5rem .75rem
}
.ui-autocomplete-suggestion:hover{background-color:rgba(0,0,0,0.06)
}
.ui-autocomplete-suggestion.is-highlighted{background-color:rgba(0,0,0,0.1)
}
.ui-autocomplete-suggestion__image{white-space:nowrap;overflow:hidden;text-overflow:ellipsis;align-items:center;display:flex
}
.ui-autocomplete-suggestion__image-object{background-position:50%;background-size:cover;border-radius:50%;height:2rem;margin-right:.75rem;width:2rem
}



