
.ui-progress-linear{display:block;height:.25rem;overflow:hidden;position:relative;transition-duration:0.3s;transition-property:height, opacity;transition-timing-function:ease;width:100%
}
.ui-progress-linear__progress-bar.is-determinate{height:.25rem;left:0;position:absolute;top:0;transform-origin:left;transition:transform 0.2s linear;width:100%
}
.ui-progress-linear__progress-bar.is-indeterminate::before{animation:ui-progress-linear-indeterminate 2.1s cubic-bezier(0.65, 0.815, 0.735, 0.395) infinite;background-color:inherit;content:'';height:.25rem;left:0;position:absolute;top:0
}
.ui-progress-linear__progress-bar.is-indeterminate::after{animation-delay:1.15s;animation:ui-progress-linear-indeterminate-short 2.1s cubic-bezier(0.165, 0.84, 0.44, 1) infinite;background-color:inherit;content:'';height:.25rem;left:0;position:absolute;top:0
}
@keyframes ui-progress-linear-indeterminate{
0%{left:-35%;right:100%
}
60%{left:100%;right:-90%
}
100%{left:100%;right:-90%
}
}
@keyframes ui-progress-linear-indeterminate-short{
0%{left:-200%;right:100%
}
60%{left:107%;right:-8%
}
100%{left:107%;right:-8%
}
}
.ui-progress-linear--transition-fade-enter-active,.ui-progress-linear--transition-fade-leave-active{transition:opacity 0.3s ease
}
.ui-progress-linear--transition-fade-enter,.ui-progress-linear--transition-fade-leave-active{opacity:0
}
.ui-progress-linear--color-primary{background-color:rgba(33,150,243,0.4)
}
.ui-progress-linear--color-primary .ui-progress-linear__progress-bar{background-color:#2196f3
}
.ui-progress-linear--color-accent{background-color:rgba(213,0,249,0.4)
}
.ui-progress-linear--color-accent .ui-progress-linear__progress-bar{background-color:#d500f9
}
.ui-progress-linear--color-black{background-color:rgba(97,97,97,0.4)
}
.ui-progress-linear--color-black .ui-progress-linear__progress-bar{background-color:#616161
}
.ui-progress-linear--color-white{background-color:rgba(255,255,255,0.4)
}
.ui-progress-linear--color-white .ui-progress-linear__progress-bar{background-color:white
}



