
.ui-toolbar{align-items:center;display:flex;font-family:Roboto,-apple-system,BlinkMacSystemFont,"Segoe UI",Oxygen,Ubuntu,Cantarell,"Fira Sans","Droid Sans",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";font-size:1.125rem;height:3.5rem;padding-left:1rem;position:relative
}
.ui-toolbar.is-raised{box-shadow:0 0 2px rgba(0,0,0,0.12),0 2px 2px rgba(0,0,0,0.2)
}
.ui-toolbar:not(.is-raised).ui-toolbar--type-default{border-bottom:.0625rem solid rgba(0,0,0,0.12)
}
.ui-toolbar .ui-icon-button{height:3rem;width:3rem
}
.ui-toolbar__left{align-items:center;display:flex;flex-shrink:0
}
.ui-toolbar__nav-icon{margin-left:-1rem;margin-right:.5rem
}
.ui-toolbar__brand{min-width:10rem
}
.ui-toolbar__brand-text{flex-grow:1;padding-right:.5rem
}
.ui-toolbar__body{display:flex;flex-grow:1
}
.ui-toolbar__body.has-brand-divider{border-left-style:solid;border-left-width:.0625rem;padding-left:1.5rem
}
.ui-toolbar__right{flex-shrink:0;margin-left:auto
}
.ui-toolbar__progress{bottom:0;height:.1875rem;left:0;position:absolute;right:0
}
.ui-toolbar--type-default{background-color:white
}
.ui-toolbar--type-colored{background-color:#2196f3
}
.ui-toolbar--type-clear{background-color:transparent;border:none;box-shadow:none
}
.ui-toolbar--text-color-black{color:rgba(0,0,0,0.87)
}
.ui-toolbar--text-color-black .ui-toolbar__body{border-left-color:rgba(0,0,0,0.15)
}
.ui-toolbar--text-color-white{color:white
}
.ui-toolbar--text-color-white .ui-toolbar__body{border-color:rgba(255,255,255,0.4)
}
.ui-toolbar--progress-position-top .ui-toolbar__progress{top:0
}



