
.ui-close-button{align-items:center;background-color:transparent;background:none;border-radius:50%;border:none;cursor:pointer;display:inline-flex;justify-content:center;margin:0;outline:none;overflow:hidden;padding:0;position:relative;-webkit-mask-image:-webkit-radial-gradient(circle, white, black)
}
