}
.ui-close-button__focus-ring{background-color:rgba(0,0,0,0.15);border-radius:50%;left:0;opacity:0;position:absolute;top:0;transform-origin:center;transform:scale(0);transition:transform 0.2s ease, opacity 0.2s ease
}
