
.ui-alert{display:flex;font-family:Roboto,-apple-system,BlinkMacSystemFont,"Segoe UI",Oxygen,Ubuntu,Cantarell,"Fira Sans","Droid Sans",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";font-size:.9375rem;line-height:1.4;margin-bottom:1rem;overflow:hidden;position:relative;transition:margin-bottom 0.3s;width:100%
}
.ui-alert a{text-decoration:none
}
.ui-alert a:hover,.ui-alert a:focus{text-decoration:underline
}
.ui-alert__icon{align-self:flex-start;flex-shrink:0;margin-right:.75rem
}
.ui-alert__body{align-items:center;color:rgba(0,0,0,0.75);display:flex;flex-direction:row;margin-bottom:0;margin-top:0;min-height:3rem;padding:.75rem 1rem;transition:opacity 0.3s, margin-top 0.4s;width:100%
}
.ui-alert__content{flex-grow:1
}
.ui-alert__dismiss-button{align-self:flex-start;flex-shrink:0;margin-bottom:-.25rem;margin-left:.5rem;margin-right:-.5rem;margin-top:-.25rem
}
.ui-alert--type-info .ui-alert__body{background-color:rgba(33,150,243,0.12)
}
.ui-alert--type-info .ui-alert__icon{color:#2196f3
}
.ui-alert--type-info a{color:#2196f3
}
.ui-alert--type-success .ui-alert__body{background-color:rgba(76,175,80,0.12)
}
