
.ui-collapsible{font-family:Roboto,-apple-system,BlinkMacSystemFont,"Segoe UI",Oxygen,Ubuntu,Cantarell,"Fira Sans","Droid Sans",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";margin-bottom:.5rem;width:100%
}
.ui-collapsible:not(.is-disabled) .ui-collapsible__header:hover,body[modality="keyboard"] .ui-collapsible:not(.is-disabled) .ui-collapsible__header:focus{background-color:#e0e0e0
}
.ui-collapsible.is-open .ui-collapsible__header-icon{transform:rotate(-180deg)
}
.ui-collapsible.is-disabled .ui-collapsible__header{cursor:default;opacity:0.6
}
.ui-collapsible.is-disabled .ui-collapsible__header-icon{cursor:default
}
.ui-collapsible__header{align-items:center;background-color:#eee;cursor:pointer;display:flex;font-size:.9375rem;line-height:1.5;margin:0;min-height:3rem;padding:.75rem 1rem;position:relative;touch-action:manipulation;width:100%
}
.ui-collapsible__header .ui-ripple-ink__ink{opacity:0.1
}
