}
.ui-collapsible__header-icon{color:rgba(0,0,0,0.54);cursor:pointer;margin-left:auto;margin-right:-.25rem;transition:transform 0.3s ease
}
.ui-collapsible__body-wrapper{height:initial;overflow:hidden
}
.ui-collapsible__body{border-top:0;border:1px solid #eee;display:block;padding:1rem;width:100%
}
