
.ui-checkbox-group{font-family:Roboto,-apple-system,BlinkMacSystemFont,"Segoe UI",Oxygen,Ubuntu,Cantarell,"Fira Sans","Droid Sans",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";line-height:normal
}
.ui-checkbox-group:not(.is-disabled):not(.is-invalid):hover .ui-checkbox-group__label-text{color:rgba(0,0,0,0.75)
}
.ui-checkbox-group:not(.is-disabled):not(.is-invalid).is-active .ui-checkbox-group__label-text{color:#2196f3
}
.ui-checkbox-group.is-vertical .ui-checkbox-group__checkboxes{flex-direction:column;padding-top:.5rem
}
.ui-checkbox-group.is-vertical .ui-checkbox-group__checkbox{margin-bottom:.75rem;margin-left:0;width:100%
}
.ui-checkbox-group.is-invalid .ui-checkbox-group__label-text{color:#f44336
}
.ui-checkbox-group.is-invalid .ui-checkbox-group__feedback{color:#f44336
}
.ui-checkbox-group.is-disabled .ui-checkbox-group__feedback{opacity:.8
}
.ui-checkbox-group__label-text{color:rgba(0,0,0,0.54);font-size:.9375rem;transition:color 0.1s ease
}
.ui-checkbox-group__checkboxes{align-items:center;display:flex;min-height:2rem
}
.ui-checkbox.ui-checkbox-group__checkbox{margin-bottom:0;margin-left:1.5rem
}
.ui-checkbox.ui-checkbox-group__checkbox:first-child{margin-left:0
}
.ui-checkbox-group__feedback{color:rgba(0,0,0,0.54);font-size:.875rem;line-height:1.2;margin:0;padding-top:0rem;position:relative
}
.ui-checkbox-group--box-position-right:not(.is-vertical) .ui-checkbox__label-text{margin-right:.5rem
}



