
.ui-radio-group{font-family:Roboto,-apple-system,BlinkMacSystemFont,"Segoe UI",Oxygen,Ubuntu,Cantarell,"Fira Sans","Droid Sans",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol"
}
.ui-radio-group:not(.is-disabled):not(.is-invalid):hover .ui-radio-group__label-text{color:rgba(0,0,0,0.75)
}
.ui-radio-group:not(.is-disabled):not(.is-invalid).is-active .ui-radio-group__label-text{color:#2196f3
}
.ui-radio-group.is-vertical .ui-radio-group__radios{flex-direction:column;padding-top:.5rem
}
.ui-radio-group.is-vertical .ui-radio-group__radio{margin-bottom:.75rem;margin-left:0;width:100%
}
.ui-radio-group.is-invalid .ui-radio-group__label-text{color:#f44336
}
.ui-radio-group.is-invalid .ui-radio-group__feedback{color:#f44336
}
.ui-radio-group.is-disabled .ui-radio-group__feedback{opacity:.8
}
.ui-radio-group__label-text{color:rgba(0,0,0,0.54);font-size:.9375rem;line-height:normal;transition:color 0.1s ease
}
.ui-radio-group__radios{align-items:center;display:flex;min-height:2rem
}
.ui-radio.ui-radio-group__radio{margin-left:1.5rem
}
.ui-radio.ui-radio-group__radio:first-child{margin-left:0
}
.ui-radio-group__feedback{color:rgba(0,0,0,0.54);font-size:.875rem;line-height:1.2;margin:0;padding-top:0rem;position:relative
}
.ui-radio-group--button-position-right:not(.is-vertical) .ui-radio__label-text{margin-right:.5rem
}



