
.centered-container {

    top:0;
    left:0;
    height:100%;
    width:100%;
    display:table;
    pointer-events: none;
    z-index:9999;
}
.centered-container-full-screen {
    position: fixed;
}
.centered {
    display: table-cell;
    vertical-align: middle;
    text-align: center;
}



