
.ui-modal{font-family:Roboto,-apple-system,BlinkMacSystemFont,"Segoe UI",Oxygen,Ubuntu,Cantarell,"Fira Sans","Droid Sans",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";font-size:.875rem
}
.ui-modal.has-footer .ui-modal__body{max-height:calc(100vh - 7.875rem)
}
.ui-modal:not(.has-footer) .ui-modal__body{padding:1rem 1.5rem 1.5rem
}
.ui-modal--is-open{overflow:hidden
}
.ui-modal__mask{background-color:rgba(0,0,0,0.5);display:table;height:100%;left:0;position:fixed;top:0;transition:opacity .3s ease;width:100%;z-index:50
}
.ui-modal__wrapper{display:table-cell;vertical-align:middle;overflow-x:hidden
}
.ui-modal__wrapper.has-dummy-scrollbar{overflow-y:scroll
}
.ui-modal__container{background-color:white;border-radius:.125rem;box-shadow:0 2px 8px rgba(0,0,0,0.33);margin:0 auto;max-height:100vh;max-width:100vw;outline:none;overflow:hidden;padding:0;transition:all .3s ease;width:33rem
}
.ui-modal__header{align-items:center;background-color:#F5F5F5;box-shadow:0 1px 1px rgba(0,0,0,0.16);display:flex;height:3.5rem;padding:0 1.5rem;position:relative;z-index:1
}
.ui-modal__header-text{align-items:center;display:flex;flex-grow:1;font-size:1.125rem;font-weight:normal;margin:0
}
.ui-modal__close-button{margin-left:auto;margin-right:-.5rem
}
.ui-modal__body{max-height:calc(100vh - 3.5rem);overflow-y:auto;padding:1rem 1.5rem
}
.ui-modal__footer{align-items:center;display:flex;height:4.375rem;justify-content:flex-end;padding:0 1.5rem
}
.ui-modal__footer .ui-button{margin-left:.5rem
}
.ui-modal__footer .ui-button:first-child{margin-left:0
}
.ui-modal--size-large .ui-modal__container{width:53rem
}
.ui-modal--size-small .ui-modal__container{width:20rem
}
.ui-modal--transition-fade-enter,.ui-modal--transition-fade-leave-active{opacity:0
}
.ui-modal--transition-scale-enter,.ui-modal--transition-scale-leave-active{opacity:0
}
.ui-modal--transition-scale-enter .ui-modal__container,.ui-modal--transition-scale-leave-active .ui-modal__container{transform:scale(1.1)
}



